.home {
	--img-url: url("../../assets/home/background-home-mobile.jpg");
	background-image: var(--img-url);
	grid-template-rows: 20% 35% 40% auto;
	padding: 2rem 1rem;
	height: 100%;
}

.home p {
	line-height: 1.5rem;
}

.explore {
	border-radius: 50%;
	aspect-ratio: 1;
	width: fit-content;
	padding: 1.8rem;
	margin: 0 auto;
}
