.crew {
	background-image: url("../../../assets/crew/background-crew-mobile.jpg");
	grid-template-rows: 3rem 13rem 1rem 14rem;
	grid-template-columns: 1fr;
	width: 100%;
}

.crew img {
	width: 9rem;
	height: 12rem;
	margin: 0 auto;
}

.crew hr {
	height: 1px;
	width: 80%;
	margin: 0 auto;
	border: none;
	opacity: 0.2;
}

.slide {
	margin: 0 auto;
	padding: 0;
}
.slide li {
	width: 0.6rem;
	height: 0.6rem;
	border-radius: 50%;
	background-color: grey;
}

.slide li:hover {
	background-color: rgb(207, 207, 207);
}

.slide [aria-selected="true"] {
	background-color: rgb(255, 255, 255);
}
