.destinationModel {
	grid-template-rows: 10rem 5rem 13rem auto auto;
	--img-url: url("../../../assets/destination/background-destination-mobile.jpg");
	background-image: var(--img-url);
}

.activeLink {
	border-bottom: 2px solid white;
	padding-bottom: 0.6rem;
}

.pic {
	width: 10rem;
	height: 10rem;
	background-image: url("../../../assets/destination/image-mars.png");
}

.destinationModel hr {
	height: 1px;
	width: 86%;
	margin: 0 auto;
	border: none;
	opacity: 0.3;
}
