.technology {
	grid-template-rows: 3rem 12rem 4rem auto;
}

.slide {
	margin: 0 auto;
	padding: 0;
}
.slide li {
	width: 0.6rem;
	height: 0.6rem;
	border-radius: 50%;
	background-color: grey;
}

.slide li:hover {
	background-color: rgb(207, 207, 207);
}

.slide [aria-selected="true"] {
	background-color: rgb(255, 255, 255);
}
